.content{
    width: 91.1%;
    margin: 0 auto 8rem auto;
    position: relative;
}

.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 1rem auto;
}

.headText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronLeft{
    width: 1.75rem;
    height: 1.75rem;
    position: absolute;
    top: 0;
    left: -5px;
    transform: translate(0%,-10%);
}

.detailContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.otherDetail{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}


.inputWrapper{
    height: 3rem;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.crossSign{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: none;
}

.crossSign.display{
    display: block;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
    background: inherit !important;
}

.input::placeholder{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.searchIcon{
    width: 1.5rem;
    height: 1.5rem;
}

.headcontent{
    display: none;
}

.desktopFCCard{
    display: none;
}

.mobileFCCard{
    width: 100%;
}

@media (min-width: 1280px){
    .content{
        width: 68.75%;
    }
    .headText{
        font-size: 23px;
    }
    .header{
        justify-content: flex-start;
        margin-bottom: 1.5rem;
    }

    .mobileFCCard{
        display: none;
    }

    .desktopFCCard{
        width: 100%;
        display: block;
    }
    .chevronLeft{
        display: none;
    }

    .detailContainer{
        width: 100%;
        /* flex-direction: row; */
        gap: 0;
        justify-content: space-between;
    }
    .cardContainer{
        width: 100%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .otherDetail{
        width: 100%;
        flex-grow: 0;
        flex-shrink: 0;
    }
    .input{
        font-size: 17px;
    }

    .input::placeholder{
        font-size: 17px;
    }
    .headcontent{
        display: block;
        width: 100%;
    }
    .Navbar{
        display: flex;
        gap: 4px;
        align-items: center;
    }
    .fleekicon{
        width: 3.5rem;
        height: 3.5rem;
    }
    .fleektitle{
        font-size: 21px;
        color: #FAFAFA;
    }
    .inputWrapper{
        height: 3.625rem;
    }
}    