.CartPage{
    background: #0F0F0F;
}

.content{
    width: 91.2%;
    margin: 0rem auto;
    height: fit-content;
    background: #0F0F0F;
}

.header{
    width: 100%;
    margin: 1rem auto 2rem auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.chevron{
    width: 1.75rem;
    height: 1.75rem;
    margin-right: auto;
}

.CartTitle{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.RemoveAll{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FF7F6B; 
    cursor: pointer;
    margin-left: auto;
}

.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.itemCount{
    display: none;
}

.desktopPayButton{
    display: none;
}

@media (min-width: 1280px) {
    .CartPage{
        width: 68.75%;
        max-width: 62rem;
        margin: 4rem auto;
    }

    .content{
        width: 100%;
    }

    .itemCount{
        display: block;
        color: #A8A8A8;
        font-size: 15px;
        line-height: 21px;
    }

    .chevron{
        display: none;
    }

    .CartTitle{
        display: flex;
        width: fit-content;
        justify-content: center;
        align-items: center;
        font-family: 'Gilroy-Bold';
        font-size: 23px;
        gap: 0.5rem;
    }
    .RemoveAll{
        font-size: 15px;
    }

    .desktopPayButton{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 3rem;
        background: #00998C;
        border-radius: 1rem;
        font-family: 'Gilroy-Bold';
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.05em;
        color: #FAFAFA;
        cursor: pointer;
    }
    .header{
        margin-bottom: 1.5rem;
    }
}    