.headcontent{
    display: none;
}

.content{
    width: 91.1%;
    margin: 0rem auto 8rem auto;
    position: relative;
}
.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    margin: 1rem auto;
}

.crossSign{
    width: 1.75rem;
    height: 1.75rem;
}

.headText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.helpText{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #53A5F4;
}
.chevronLeft{
    width: 1.75rem;
    height: 1.75rem;
}

.detailContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.otherDetail{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.orderDetail{
    width: 100%;
    background: #1F1F1F;
    border-radius: 16px;
    padding: 1.5rem 1rem 1rem 1rem;
    line-height: 140%;
    letter-spacing: 0.025em;
}

.orderId{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    color: #A8A8A8;
}

.orderPlaceText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    color: #FAFAFA;
    margin-top: 1rem;
}

.date{
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    color: #A8A8A8;
}
.mobileButtonContainer{
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 29px 16px 42px 16px;
    background: #191919;
    backdrop-filter: blur(10px);
    border-radius: 32px 32px 0px 0px;
    width: 100vw;
}

.nextButton{
    width: 91.1%;
    margin: 0 auto;
    height: 3rem;
    background: #00998C;
    border-radius: 16px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
    padding: 0 1rem;
}

.forward{
    width: 1.5rem;
    height: 1.5rem;
}

@media (min-width: 1280px){
    .content{
        width: 68.75%;
    }
    .headcontent{
        display: block;
        width: 100%;
    }
    .Navbar{
        display: flex;
        gap: 4px;
        align-items: center;
    }
    .fleekicon{
        width: 3.5rem;
        height: 3.5rem;
    }
    .fleektitle{
        font-size: 21px;
        color: #FAFAFA;
    }
    .header{
        margin-bottom: 1.5rem;
    }
    .chevronLeft{
        display: none;
    }
    .headText{
        font-size: 23px;
    }
    .helpText{
        font-size: 15px;
        cursor: pointer;
    }
    .detailContainer{
        width: 100%;
        flex-direction: row;
        gap: 0;
        justify-content: space-between;
    }
    .cardContainer{
        width: 57.15%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .otherDetail{
        width: 40.11%;
        flex-grow: 0;
        flex-shrink: 0;
    }
    .orderId{
        font-size: 17px;
    }
    .orderPlaceText{
        font-size: 17px;
    }
    .date{
        font-size: 15px;
    }
    .mobileButtonContainer{
        position: relative;
        width: 100%;
        padding: 0;
        background: transparent;
    }
    .payButton{
        width: 100%;
        cursor: pointer;
    }
    .nextButton{
        width: 100%;
        cursor: pointer;
    }
}