.card{
    width: 100%;
    padding: 1rem;
    margin: 0rem auto;
    background: #1F1F1F;
    border-radius: 1rem;
    height: fit-content;
}   

.mainInfoContainer{
    display: flex;
    gap: 1rem;
}

.imageOptionContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    align-items: center;
    width: 6rem;
    flex-grow: 0;
    flex-shrink: 0;
    height: fit-content;
}

.cartImage{
    width: 100%;
    height: 6rem;
    border-radius: 0.5rem;
    object-fit: cover;
}

.priceContainer{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.priceContainer img{
    width: 1rem;
    height: 1rem;
}

.priceButton{
    width: 1.75rem;
    height: 1.75rem;
    border-radius: 50%;
    background: #2E2E2E;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    cursor: pointer;
}

.qnty{
    font-family: 'Gilroy-Medium';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}


.infoConatiner{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 0.2rem;
}

.brandName{
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.productName{
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #E2E2E2;
    max-height: 3.375rem;
    overflow: hidden;
    margin-bottom: auto;
}

.withoutFleekCoin{
    margin-top: auto;
    display: flex;
    gap: 0.5rem;
    align-items: center;
    justify-content: flex-start;
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
}

.paidPrice{
    font-family: 'Gilroy-Bold';
    color: #00C2B1;

}

.mainPrice{
    font-family: 'Gilroy-Regular';
    text-decoration-line: line-through;
    color: #A8A8A8;
}


.seperator{
    width: 100%;
    border: 0.5px solid #383838;
    margin: 1rem auto;
}

.freeSection{
    padding: 0.5rem;
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

.freeImage{
    width: 3rem;
    height: 1.5rem;
    object-fit: contain;
}

.freeText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FFFFFF;
}


.withFleekCoin{
    margin-top: auto;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    width: fit-content;
    align-items: flex-start;
}

.flex_row{
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    align-items: center;
}

.flex_row.priceFont{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
}

.flex_row.discountFont{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FFC747;
    width: fit-content;
    gap: 0.1rem;

}

.flex_row.discountFont img{
    width: 1.5rem;
    height: 0.813rem;
    object-fit: contain;
    margin-bottom: 2px;
}

.deskMainPrice{
    text-decoration-line: line-through;
    color: #A8A8A8;
}

.discount{
    color: #06BC64;
} 

@media (min-width: 1280px){
    .card{
        width: 100%;

    }

    .brandName{
        font-size: 15px;
    }

    .productName{
        font-size: 17px;
    }
    .withoutFleekCoin{
        font-size: 17px;
    }

    .withFleekCoin{
        margin-top: auto;
        display: flex;
        flex-direction: column;
        gap: 0.2rem;
        width: fit-content;
        align-items: flex-start;
        font-size: 17px;
    }

    .flex_row{
        display: flex;
        justify-content: center;
        gap: 0.5rem;
        align-items: center;
    }

    .flex_row.priceFont{
        font-family: 'Gilroy-Medium';
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
    }

    .flex_row.discountFont{
        font-family: 'Gilroy-Bold';
        font-size: 19px;
        line-height: 140%;
        letter-spacing: 0.05em;
        color: #FFC747;
        width: fit-content;
        gap: 0.1rem;

    }

    .flex_row.discountFont img{
        width: 1.5rem;
        height: 0.813rem;
        object-fit: contain;
        margin-bottom: 2px;
    }

    .deskMainPrice{
        text-decoration-line: line-through;
        color: #A8A8A8;
    }

    .discount{
        color: #06BC64;
    }

    .freeText{
        font-size: 17px;
    }
}    