.card{
    width: 100%;
    padding: 1rem 1rem 1.5rem 1rem;
    background: #1F1F1F;
    border-radius: 16px;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
}

.videoPlayerContainer{
    width: 100%;
    margin-top: 12px;
}

.videoPlayerContainer .ytp-show-cards-title{
    display: none !important;
}

@media (min-width: 1280px){
   .title{
    font-size: 17px;
   } 
   .videoPlayerContainer{
    width: 20rem;
    margin: 12px auto 0 auto;
   }
}