.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2; 
    background-color: rgba(0,0,0,0.75);
}

.popUp{
    width: 91.1%;
    height: fit-content;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 3.625rem 1.625rem 2.25rem 1.625rem;
    background: #1F1F1F;
    border-radius: 16px;
}

.greenTick{
    width: 6.5rem;
    height: 6.5rem;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-60%);
}

.title{
    font-family: 'Gilroy-Medium';
    font-size: 23px;
    line-height: 140%;
    color: #FFFFFF;
}

.paymentDetail1{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    margin-top: 1rem;
}

.fleekCoin{
    width: 1.125rem;
    height: 1.125rem;
    transform: translate(5px, 4px);
}

.paymentDetail2{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FFE099;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 1rem;
}

.boldText{
    font-family: 'Gilroy-Bold';
}

.mobileRedirectText{
    position: absolute;
    bottom: 1rem;
    font-family: 'Gilroy-Medium';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    text-align: center;
    left: 50%;
    transform: translate(-50%,0%);
    width: 100%;
}

.boldWhite{
    color: #FAFAFA;
    font-family: 'Gilroy-Bold';
}

.CrossSign{
    display: none;
}

@media (min-width: 1280px){
    .popUp{
        position: relative;
        width: 36.5rem;
        padding: 14.625rem 1.625rem 5.25rem 1.625rem;
    }

    .CrossSign{
        display: block;
        position: absolute;
        top: 1rem;
        right: 1rem;
        width: 1.75rem;
        height: 1.75rem;
        cursor: pointer;
    }

    .greenTick{
        transform: translate(-50%,90%);
    }

    .mobileRedirectText{
        display: none;
    }
}