.container{
    width: 100%;
}

.nameBox{
    width: 100;
    height: 11.625rem;
    background: #1F1F1F;
    border-radius: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin-bottom: 1.5rem;
}

.userImgContainer{
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 50%;
    background: #404040;
    flex-grow: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 2rem;
}

.userImgLetter{
    font-family: 'Gilroy-Medium';
    font-size: 23px;
    line-height: 140%;
    color: #FAFAFA;
}

.userMainImg{
    width: 100%;
    height: 100%;
}

.name{
    font-family: 'Gilroy-Medium';
    font-size: 19px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin-bottom: 2px;
}

.mobileNumber{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.02em;
    color: #A8A8A8;
}

.pencilIcon{
    width: 2rem;
    height: 2rem;
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    cursor: pointer;
}

.detailContainer{
    padding: 1.5rem 1.5rem 1.5rem 4.625rem;
    background-color: inherit;
    border: 1px solid #616161;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    gap: 1.6rem;
}

.row{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
}

.column1{
    width: 30%;
    flex-grow: 0;
    font-family: 'Gilroy-Medium';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
}

.column2{
    width: 50%;
    flex-grow: 0;
    font-family: 'Gilroy-Medium';
    font-size: 19px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;

}

.editProfileTitle{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.editDetailContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.inputBundle{
    width: 100%;
}

.inputTitle{
    font-family: 'Gilroy-Regular';
    font-size: 15px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    margin-bottom: 4px;
}
.inputWrapper{
    height: 3rem;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.inputWrapper.disable{
    border: 0.5px solid #525252;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
}

.input.disable{
    height: fit-content;
    color: #A8A8A8;
}

.input::placeholder{
    font-family: 'Gilroy-Regular';
    font-size: 15px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.input::-webkit-calendar-picker-indicator{ 
    filter: invert(100%);
}


.countryCode{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #A8A8A8;
}


.chipsContainer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
}

.chip{
    border: 1px solid #A8A8A8;
    border-radius: 48px;
    background-color: inherit;
    font-family: 'Gilroy-Medium';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 16px;

}

.chip.active{
    font-family: 'Gilroy-Bold';
    background: #00998C;
    border: none;
    color: #FAFAFA;
}

.saveButton{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    margin-top: 1rem;
    margin-bottom: 3rem;
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    cursor: pointer;
    background: #00998C;
    border-radius: 16px;
    height: 3rem;
}