.marketplace{
    width: 100%;
}

.headcontent{
    width: 91.1%;
    margin: 0 auto ;
}

.Navbar{
    display: flex;
    gap: 4px;
    align-items: center;
    margin-bottom: 2rem;
}

.fleekicon{
    width: 2.625rem;
    height: 2.625rem;
}

.fleektitle{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.bannerImage{
    width: 100%;
    height: 9.375rem;
    background: linear-gradient(180deg, #55D3A6 0%, #3AB0E2 100%);
    border-radius: 1rem;
    object-fit: cover;
}

.restContent{
    width: 91.1%;
    margin: 2rem auto 0 auto;
}

.sectionTitle{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.cardContainer{
    margin-top: 1.5rem;
    display: grid;
    width: 100%;
    /* justify-content: center;
    align-items: center;
    flex-wrap: wrap; */
    gap: 0.9rem;
    grid-template-columns: repeat(auto-fill, minmax(9.75rem, 1fr));
    justify-content: center;
    justify-items: center;
}

.sliderWrapper{
    width: 100%;
    height: fit-content;
}

.hide{
    display: none;
}

.dot{
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #545454;
}

.dot.active{
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #00AC9D;
    cursor: pointer;
}

.dotContainer{
    width: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.4rem auto 0 auto;
    gap: 4px;
    cursor: pointer;
}

.walletContainer{
    margin:-0.5rem 0 1.5rem 0;
}

@media (min-width: 1280px){
    .headcontent{
        width: 95%;
        margin: 0 auto;
        max-width: 85.5rem;
    }
    .fleekicon{
        width: 3.5rem;
        height: 3.5rem;
    }
    .fleektitle{
        font-size: 21px;
    }
    .Navbar{
        margin-bottom: 1.5rem;
    }

    .restContent{
        width: 68.75%;
        max-width: 61.875rem;
    }

    .sectionTitle{
        font-size: 23px;
    }

    .cardContainer{
        grid-template-columns: repeat(4, 11.25rem);
        row-gap: 1.5rem;
        justify-content: space-between;
        justify-items: flex-start;
    }

    .bannerImage{
        height: 29.375rem;
        object-fit: fill;
        border-radius: 4.5rem;
    }

    .dotContainer{
        margin: 1rem auto 0 auto;
    }

    .dot{
        width: 8px;
        height: 8px;
    }

    .dot.active{
        width: 12px;
        height: 12px;
    }

    .walletContainer{
        display: none;
    }
}