.content{
    width: 91.1%;
    margin: 1.5rem auto;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 48px);
}

.elements{
    width: 100%;
}

.upperContent{
    width: 100%;
    flex: 0 1 auto;
}

.chevron{
    width: 1.75rem;
    height: 1.75rem;
    margin-bottom: 1rem;
    margin-left: -0.5rem;
}

.uppertitle{
    font-family: 'Gilroy-Bold';
    font-size: 19px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.uppersubTitle{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    margin-bottom: 1.5rem;
}

.form{
    width: 100%;
    height: 100%;
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    gap: 1rem;
}

.formGroup{
    width: 100%;
}

.title{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.inputWrapper{
    height: 3rem;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4px;
}



.countryCode{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
}

.crossSign{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: none;
}

.crossSign.display{
    display: block;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
   
}

.input::placeholder{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.Button{
    height: 3rem;
    width: 100%;
    background: #2E2E2E;
    border-radius: 1rem;
    text-align: center;
    margin-top: auto;
    border: none;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    font-family: 'Gilroy-Bold';
    color: #616161;
}

.Button.active{
    background: #00998C;
    color: #FAFAFA;
    cursor: pointer;
}

.belowText{
    width: 100%;
    flex: 0 1 2.25rem;
    margin-top: 0.75rem;
    text-align: center;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #808080;
    margin-top: 1.5rem;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    align-items: center;
}

.pointerText{
    cursor: pointer;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #53A5F4;
}

@media (min-width: 1280px){
    .upperContent{
        display: none;
    }
    .content{
        width: 100%;
        height: 100vh;
        margin: 0;
        background: #191919; 
    }
    .elements{
        width: 20.5rem;
        height: fit-content;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-55%);
        -ms-transform: translate(-50%,-55%);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .input{
        font-size: 15px;
    }
    .input::placeholder{
        font-size: 15px;
    }
}