.card{
    width: 328px;
    padding: 0.5rem 0.5rem 1rem 0.5rem;
    margin: 1rem auto;
    background: #1F1F1F;
    border-radius: 1rem;
    height: fit-content;
} 

.bundleImgContainer{
    width: 100%;
    height: 9.375rem;
    position: relative;
}

.brandTag{
    padding: 3px 6px;
    background: #A1804F;
    border-radius: 0.5rem;
    color: #FAFAFA;
    font-family: 'Gilroy-Bold';
    font-size: 10px;
    line-height: 120%;
    letter-spacing: 0.05em;
    position: absolute;
    top: 1rem;
    right: 1rem;
}

.bundleImage{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0.75rem;
}

.infoContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 0.8rem;
    gap: 0.8rem;
}

.logo{
    width: 2rem;
    height: 2rem;
    object-fit: cover;
}

.nameDetail{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: 'Gilroy-Medium';
}

.planName{
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.planValidity{
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;    
    color: #A8A8A8;
}

.planPrice{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    text-align: right;
    letter-spacing: 0.05em;
    color: #00C2B1;
    margin-left: auto;
}

.detailContainer{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #808080;
    padding: 0 1rem 0 0.2rem;
    margin-top: 0.8rem;
}

@media (min-width: 1280px){
    .card{
        width: 32rem;
        padding: 1rem;
    }

    .bundleImgContainer{
        height: 14.125rem;
    }

    .brandTag{
        top: 0.5rem;
        right: 0.5rem;
        position: absolute;
        width: fit-content;
    }

    .planName{
        font-size: 19px;
    }

    .planValidity{
        font-size: 15px;
    }

    .planPrice{
        font-size: 17px;
    }

    .detailContainer{
        font-size: 15px;
    }
}    