.card{
    width: 100%;
    background: #1F1F1F;
    border-radius: 1rem;
    padding: 1.625rem 1rem 1rem 1rem;
    display: flex;
    flex-direction: column;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
    margin-bottom: 0.75rem;
}

.payemntInstrument{
    width: 100%;
    display: flex;
    align-items: center;
}

.cardImage{
    width: 1.75rem;
    height: 1.75rem;
    flex-grow: 0;
    background-color: white;
    margin-right: 1.125rem;
}

.cardDetail{
    width: 48.8%;
    flex-grow: 0;
    display: flex;
    flex-direction: column;
}

.cardName{
    width: 100%;
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.cardNumber{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.securedDiv{
    height: 1.5rem;
    width: fit-content;
    margin-left: auto;
    background: rgba(6, 188, 100, 0.4);
    border-radius: 48px;
    padding: 0 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
}

.sheild{
    width: 1rem;
    height: 1rem;
}

.SecuredText{
    font-family: 'Gilroy-Medium';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

@media (min-width: 1280px){
    .title{
        font-size: 17px;
    }

    .cardName{
        font-size: 17px;
    }

    .cardNumber{
        font-size: 15px;
    }

    .SecuredText{
        font-size: 15px;
    }
}