.FaqCard{
    width: 100%;
    margin: 0 auto;
    background-color: #191919;
    border-radius: 0.75rem;
    padding: 1rem 0.8rem;
}

.ActionArea{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Title{
    font-family: 'Gilroy-Bold';
    font-size: 1rem;
    line-height: 1.25rem;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronDownClass{
    width: 1.75rem;
    height: 1.75rem;
}

.faqsContainer{
    padding: 0rem 0.2rem;
    margin-top: 4px;
}

@media (min-width: 1280px) {
    .FaqCard{
        width: 100%;
        padding: 1.5rem;
        position: relative;
        
    }

    .ActionArea{
        cursor: pointer;
    }
}    