.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 1rem auto 2rem auto;
}

.headText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronLeft{
    width: 1.75rem;
    height: 1.75rem;
    position: absolute;
    top: 0;
    left: -5px;
    transform: translate(0%,-10%);
}

.text{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    text-align: center;
    margin-top: 1.5rem;
}

.actionContainer{
    padding: 19px 16px;
    background: #1F1F1F;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.icon{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    flex-grow: 0;
    margin-right: 1rem;
}

.textContainer{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.text1{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.text2{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.actionText{
    flex-grow: 0;
    margin-left: auto;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    text-align: right;
    letter-spacing: 0.05em;
    color: #00C2B1;

}

.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}


@media (min-width: 1280px){
    .header{
        display: none;
    }


    .icon{
        width: 3rem;
        height: 3rem;
        border-radius: 50%;
        flex-grow: 0;
        margin-right: 0.5rem;
    }

    .text1{
        font-size: 17px;
    }

    .text2{
        font-size: 15px;
    }

    .actionText{
        font-size: 15px;
    }

    .text{
        font-size: 15px;
    }
}