
.brandInfoContainer{
    width: 100%;
}

.brandImageContainer{
    width: 100%;
    position: relative;
}

.bannerNav{
    width: 100vw;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 0.3rem;
    padding: 0 0.5rem;
}

.brandImageContainer>img{
    width: 100vw;
    height: 12.5rem;
    object-fit: cover;
}

.infoContainer{
    width: 91.2%;
    margin: 0rem auto 1rem auto;
    border-radius: 16px;
    padding: 1rem;
    background: #1F1F1F;
    transform: translateY(-1.5rem);
}

.LogoNameDetail{
    display: flex;
    align-items: center;
    justify-content: center;
}

.LogoImageContainer{
    width: 3.5rem;
    height: 3.5rem;
    margin-right: 1rem;
}

.LogoImageContainer>img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 50%;
}

.brandName{
    font-family: 'Gilroy-Bold';
    font-size: 1rem;
    line-height: 1.5rem;
    letter-spacing: 0.005em;
    color: #EEEEEE;
}

.shareLogo{
    width: 1.25rem;
    height: 1.25rem;
    margin-left: auto;
}

.brandinfoText{
    padding-right: 1rem;
    width: 100%;
    margin-top: 1rem;
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.025em;

}

.brandinfoText>div{
    color: #E2E2E2;
}

.brandinfoText>div>span{
    color: rgb(68, 68, 191);
    cursor: pointer;
}

.chevronLeft{
    width: 24px;
    height: 24px;
}

.cartIcon{
    width: 32px;
    height: 32px;
}

@media (min-width: 1280px) {
    .brandImageContainer>img{
        height: 30.5rem;
        border-radius: 1rem;
        object-fit: fill;
        width: 100%;
        /* height: auto; */
    }
    .infoContainer{
        width: 100%;
        margin: 1rem auto 1rem auto;
        border-radius: 16px;
        padding: 1rem;
        background: #1F1F1F;
        transform: translateY(0rem);
    }
    .brandName{
        font-family: 'Gilroy-Bold';
        font-size: 1.313rem;
        line-height: 1.813rem;
        letter-spacing: 0.005em;
        color: #FAFAFA;
    }
    .brandinfoText{
        font-size: 1.063rem;
        line-height: 1.5rem;
        font-family: 'Gilroy-Medium';
    }
    .bannerNav{
        display: none;
    }
}    