.NavBar{
    width: 91.1%;
    max-width: 82rem;
    display: flex;
    gap: 0.75rem;
    align-items: center;
    margin: 1rem auto 1rem auto;
}

.fleekicon{
    width: 2.625rem;
    height: 2.625rem;
}



.fleektitle{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin-right: auto;
}

.rightContent{
    display: flex;
    gap: 0.75rem;
    align-items: center;
}

.iconContainer{
    width: 2.5rem;
    height: 2.5rem;
    background: #1F1F1F;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.icon{
    width: 1.5rem;
    height: 1.5rem;
}

.hideInMob{
    display: none;
    width: fit-content;
    height: fit-content;
}

.manageSubContainer{
    height: 3rem;
    padding: 0 0.625rem;
    background: #1F1F1F;
    border-radius: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    cursor: pointer;
}

@media (min-width: 1280px){
    .NavBar{
        width: 95%;
        max-width: 85.5rem;
        margin: 1.5rem auto;
        gap: 1rem;
    }
    .fleekicon{
        width: 3.5rem;
        height: 3.5rem;
    }

    .fleekicon1{
        width: 2rem;
        height: 2rem;
    }
    .fleektitle{
        font-size: 21px;
    }

    .icon{
        width: 2rem;
        height: 2rem;
    }
    .iconContainer{
        width: 3rem;
        height: 3rem;
    }

    .rightContent{
        gap: 1rem;
    }

    .hideInMob{
        display: block;
    }

    .subText{
        font-family: 'Gilroy-Bold';
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #FAFAFA;
    }

    .avatar{
        height: 3rem;
        width: 3rem;
        border-radius: 50%;
    }
}