.card{
    width: 100%;
    padding: 1rem;
    margin: 0rem auto;
    background: #1F1F1F;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.productImg{
    width: 5.25rem;
    height: 5.25rem;
    border-radius: 0.5rem;
}

.info{
    width: 9.75rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 0.2rem;
    margin-right: auto;
}

.title{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.detail{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.price{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #00C2B1;
}

.chevron{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
}

@media (min-width: 1280px){
    .card{
        width: 100%;
        align-items: flex-start;
    }
    .productImg{
        width: 10.25rem;
        height: 10.25rem;
    }

    .chevron{
        width: 1.75rem;
        height: 1.75rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .info{
        width: 15rem;
    }

    .title{
        font-size: 21px;
    }

    .detail{
        -webkit-line-clamp: 3;
        font-size: 15;
    }

    .price{
        font-size: 21;
        margin-top: 2rem;
        margin-bottom: 1rem;
    }
}