.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    z-index: 2; 
}

.popUp{
    width: 100%;
    background: #191919;
    border-radius: 1.5rem 1.5rem 0px 0px;
    padding: 1.5rem 1rem;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.redirectingText{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.secondText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #53A5F4;
}

.infoContainer{
    padding: 1rem;
    margin-top: 2rem;
    background: #1F1F1F;
    border-radius: 1rem;
}

.nextStepText{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
    margin-bottom: 1rem;
}

.nextInstruction{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.messageLogo{
    width: 1.75rem;
    height: 1.75rem;
    margin-right: 1rem;
}

@media (min-width: 1280px){
    .popUp{
        width: 22.5rem;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
    }
    
}