.card{
    padding: 1.125rem 1rem;
    background: #1F1F1F;
    border-radius: 1rem;
    display: flex;
    justify-content: flex-start;
    width: 91.1%;
    margin: 0 auto;
    gap: 0.625rem;
    align-items: center;
    cursor: pointer;
}

.discountIcon{
    width: 1.75rem;
    height: 1.75rem;
}

.applyCoupanText{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 120%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
}

.newText{
    width: 2.875rem;
    height: 1.375rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 224, 153, 0.1);
    border-radius: 8px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FFC747;
}

.chevronRight{
    width: 1.5rem;
    height: 1.5rem;
    margin-left: auto;
}

@media (min-width: 1280px){
    .card{
        width: 22.125rem;
        padding: 1.125rem 1.875rem;
    }

    .applyCoupanText{
        font-size: 17px;
    }
}