.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    z-index: 2; 
}

.popUp{
    width: 100%;
    background: #191919;
    border-radius: 1.5rem 1.5rem 0px 0px;
    padding: 1.25rem 1rem;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.detail{
    width: 100%;
}
.title{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.title1{
    font-family: 'Gilroy-Bold';
    font-size: 23px;
    line-height: 140%;
    color: #FAFAFA;
}

.title2{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    margin-bottom: 1.875rem;
}

.phoneTitle{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.inputWrapper{
    height: 3rem;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}



.countryCode{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
}

.crossSign{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: none;
}

.crossSign.display{
    display: block;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
}

.continueButton{
    height: 3rem;
    border-radius: 1rem;
    width: 100%;
    border: none;
    background-color: #2E2E2E;
    margin-top: 2rem;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.05em;
    color: #616161;
    pointer-events: none;
    cursor: not-allowed;
}

.continueButton.active{
    color: #FAFAFA;
    background-color: #00998C;
    pointer-events: all;
    cursor: pointer;
}


@media (min-width: 1280px){
    .overlay{
        background: #191919;
    }

    .detail{
        display: none;
    }
    .popUp{
        background: inherit;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-55%);
        -ms-transform: translate(-50%,-55%);
        width: 20.5rem;
    }
}