.card{
    width: 100%;
    padding: 1rem;
    margin: 0rem auto;
    background: #1F1F1F;
    border-radius: 1rem;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    gap: 1rem;
}  

.brandImgContainer{
    width: 6rem;
    height: 6rem;
}

.brandImgContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0.5rem;
}

.detailContainer{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.brandName{
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.brandValidity{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #E2E2E2;
    margin-top: 0.4rem;
    padding-right: 0.8rem;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    max-height: 3.375rem;
}

.freeText{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FF56EE;
    margin-top: auto;
}

@media (min-width: 1280px){
    .card{
        width: 100%;
    }

    .brandName{
        font-size: 15px;
    }

    .brandValidity{
        font-size: 17px;
        margin-top: 0.2rem;
    }
}    