.LoginPage{
    background: #0F0F0F;
    width: 100%;
    height: 100vh;
}

.mobileLogin{  
    width: 100%;
    height: 100%;
}

.loginHome{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.backCard{
    height: 14rem;
    width: 100%;
    background: #00998C;
    padding: 1rem 0.875rem;
}

.cheveron{
    width: 1.75rem;
    height: 1.75rem;
    margin-bottom: 1.2rem;
    margin-left: -0.5rem;
}

.welcomeText{
    font-family: 'Gilroy-Bold';
    font-size: 23px;
    line-height: 140%;
    color: #FAFAFA;
    margin-bottom: 0.5rem;
}

.welcomeSubText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.optionContainer{
    width: 91.2%;
    margin: 0 auto;
    padding: 1rem;
    background: #191919;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    flex-direction: column;
    color: #FAFAFA;
    transform: translateY(-5rem);
}

.getStarted{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
}

.actionButton{
    width: 100%;
    height: 3rem;
    border: 1px solid #A8A8A8;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    cursor: pointer;
}

.OrText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.whatsAppIcon{
    width: 1.5rem;
    height: 1.5rem;
}

.mobIcon{
    width: 1.5rem;
    height: 1.5rem;
}

.termContainer{
    width: 91.2%;
    margin: auto auto 2rem auto;
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    display: flex;
    flex-direction: column;
}

.termsButton{
    color: #53A5F4;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@media (min-width: 1280px){
    .LoginPage{
        background: #191919;
        width: 100%;
        height: 100vh;
    }
    .loginHome{
        width: fit-content;
        height: fit-content;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-55%);
        -ms-transform: translate(-50%,-55%);
    }

    .backCard{
        display: none;
    }

    .termContainer{
        display: none;
    }

    .optionContainer{
        width: 22.5rem;
        padding: 2rem;
        transform: translateY(0rem);
    }
    .getStarted{
        font-size: 23px;
    }
}