.errorScreen{
    width: 100%;
    height: 100vh;
    background: #0F0F0F;
}

.content{
    position: relative;
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-55%);
    -ms-transform: translate(-50%,-55%);
}

.errorImage{
    width: 13.875rem;
    height: 12.938rem;

}

.headerText{
    margin-top: 1rem;
    font-family: 'Gilroy-Medium';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.subText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #808080;
    margin-top: 0.5rem;
}


.button{
    width: 9.75rem;
    height: 3rem;
    border-radius: 1rem;
    background: #00998C;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    margin-top: 1.5rem;
}

@media (min-width: 1280px){
    .button{
        width: 20.5rem;
    }
}