.Container{
    width: 100%;
    margin-top: 1rem;
}

.ActionableArea{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Question{
    width: 15rem;
    font-family: 'Gilroy-Medium';
    letter-spacing: 0.02em;
    color: #FAFAFA;
    font-size: 1rem;
    line-height: 1.313rem;
}

.Icon{
    width: 1.5rem;
    height: 1.5rem;
}

.Answer{
    font-family: 'Gilroy-Medium';
    color: #A8A8A8;
    font-size: 0.875rem;
    letter-spacing: 0.025em;
    line-height: 1.125rem;
    margin-top: 4px;
}

@media (min-width: 1280px) {
    .Question{
        font-size: 1.125rem;
    }
    .Answer{
        font-size: 1rem;
    }
    .ActionableArea{
        cursor: pointer;
    }
}    