.card{
    background: linear-gradient(89.81deg, #6186AD 0.16%, #425A7A 99.23%);
    border-radius: 16px;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.InstallFleek{
    width: 6.125rem;
    height: 4.25rem;
    flex-grow: 0;
}

.flexColumn{
    display: flex;
    flex-direction: column;
    gap: 1.125rem;
    flex-grow: 1;
}

.text{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.installButton{
    background: #FAFAFA;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    width: 8.375rem;
    height: 30px;
    cursor: pointer;
}

.installText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #0F0F0F;
}

.ArrowCircleRight{
    width: 1rem;
    height: 1rem;
}