.card{
    width: 100%;
    padding: 1rem;
    background: #1F1F1F;
    border-radius: 1rem;
    height: fit-content;
    display: flex;
    gap: 1.125rem;
}  

.custom_checkbox{
    cursor: pointer;
}

.custom_checkbox span {
    width: 1.125rem;
    height: 1.125rem;
    border: 2px solid #00C2B1;
    display: inline-block;
    border-radius: 4px;
    /* transition: all 0.2s ease; */
}
  
.custom_checkbox input {
    display: none;
}
  
.checkBoxIcon{
    width: 100%;
    height: 110%;
    transform: translateY(-9%);
}

.coinDetail{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}

.coinDetail1{
    display: flex;
    width: fit-content;
    align-items: center;
    gap: 0.5rem;
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.coinDetail2{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.bold{
    font-family: 'Gilroy-Bold';
}

.FleekCoin{
    width: 14px;
    height: 14px;
}

.coinColor{
    color: #FFC747;
}