.card{
    width: 100%;
    padding: 1rem;
    margin: 0rem auto;
    background: #1F1F1F;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.5rem;
}

.info{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.heading{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FFFFFF;
}

.chevron{
    width: 28px;
    height: 28px;
    cursor: pointer;
}

.sliderWrapper{
    width: 100%;
    height: fit-content;
}

.sliderImg{
    width: 100%;
    height: 18.5rem;
    border-radius: 8px;
    object-fit: cover;
}

.arrow{
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    background: rgba(36, 36, 36, 0.8);
    z-index: 2;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.arrow.left{
    left: 2px;
}

.arrow.right{
    right: 2px;
}

.arrow.hide{
    display: none;
}

.arrow img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.bottomContainer{
    width: 100%;
    height: fit-content;
}

.detail{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    margin-bottom: 6px;
}

.price{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #00C2B1;
}

@media (min-width: 1280px){
    .card{
        width: 100%;
        align-items: flex-start;
    }

    .heading{
        font-size: 21px;
    }

    .detail{
        font-size: 15px;
    }

    .price{
        font-size: 21px;
    }
    .sliderWrapper{
        width: 99%;
    }
    
}    