.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    z-index: 2; 
}

.popUp{
    width: 100%;
    background: #191919;
    border-radius: 1.5rem 1.5rem 0px 0px;
    padding: 0 1rem;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ageSign{
    width: 3.25rem;
    height: 3rem;
    margin: 3.375rem auto 1.5rem auto;
}

.headText{
    width: 65%;
    margin: 0 auto;
    text-align: center;
    color: #FAFAFA;
    font-size: 1.188rem;
    line-height: 1.688rem;
    letter-spacing: 0.02em;
    font-family: 'Gilroy-Bold';
}

.disclamerText{
    font-family: 'Gilroy-Medium';
    font-size: 0.75rem;
    line-height: 1.25rem;
    font-family: 'Gilroy-Medium';
    text-align: center;
    color: #A8A8A8;
    letter-spacing: 0.025em;
    margin: 1.5rem 0;
}

.separator{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.5rem;
    width: 100%;
}

.bar{
    width: 41.7%;
    background-color: #383838;
    height: 1px;
}

.thanksImg{
    width: 13px;
    height: 18px;
}

.buttonCollection{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}


.cutButton{
    width: 35%;
    border: 1px solid #A8A8A8;
    border-radius: 1rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.875rem;
    color: #FAFAFA;
    line-height: 1.25rem;
    letter-spacing: 0.05em;
    background: inherit;
    font-family: 'Gilroy-Bold';

}

.acceptButton{
    width: 57%;
    border: none;
    border-radius: 1rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.875rem;
    color: #FAFAFA;
    line-height: 1.25rem;
    letter-spacing: 0.05em;
    background: #00998C;
    font-family: 'Gilroy-Bold';

}

.bar2{
    width: 38%;
    background: #FFFFFF;
    border-radius: 5px;   
    height: 5px;
    margin-top: 1.25rem;
}

@media (min-width: 1280px) {
    .popUp{
        width: 27.75rem;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
    }

    .bar2{
        opacity: 0;
    }

    .cutButton{
        cursor: pointer;
    }

    .acceptButton{
        cursor: pointer;
    }
} 