.card{
    width: 9.75rem;
    height: fit-content;
    text-decoration: none;
}

.imageContainer{
    width: 100%;
    height: 10.625rem;
    border-radius: 1rem;
    position: relative;
}

.cardImage{
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    object-fit: cover;
}

.cardSubTextContainer{
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0rem 1rem 0rem 1rem;
    background: rgba(31, 31, 31, 0.8);
    padding: 6px 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
}

.cardSubText{
    font-family: 'Gilroy-Bold';
    font-size: 11px;
    line-height: 120%;
    letter-spacing: 0.025em;
    color: #FFC747;
}

.FleekCoin{
    width: 10px;
    height: 10px;
    margin-bottom: 2px;
}

.detailContainer{
    width: 100%;
    margin: 0.5rem 0 0 0;
    display: flex;
    justify-content: flex-start;
    gap: 0.5rem;
    align-items: center;
}

.logo{
    flex-grow: 0;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
}

.detail{
    flex-grow: 0;
    display: flex;
    flex-direction: column;
    gap: 2px;
    align-items: flex-start;
}

.planName{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.planPrice{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    text-align: right;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

@media (min-width: 1280px){
    .card{
        width: 11.25rem;
        text-decoration: none;

    }
    .imageContainer{
        height: 12.75rem;
    }

    .logo{
        width: 3rem;
        height: 3rem;
    }
    .planName{
        font-size: 19px;
    }

    .planPrice{
        font-size: 17px;
    }
    .cardSubText{
        font-size: 15px;
    }
    .FleekCoin{
        width: 12px;
        height: 12px;
    }
    .detailContainer{
        margin-top: 1rem;
    }
    .cardImage{
        object-fit: fill;
    }
}