
.personalDetailCard{
    width: 100%;
    margin: 0 auto;
    background: #1F1F1F;
    border-radius: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #A8A8A8;
}


.inputWrapper{
    height: 3rem;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}



.countryCode{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
}

.crossSign{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: none;
}

.crossSign.display{
    display: block;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
    background: inherit !important;
}

.input::placeholder{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

@media (min-width: 1280px){
    .personalDetailCard{
        width: 100%;
    }

    .title{
        font-size: 17px;
    }

    .countryCode{
        font-size: 15px;
    }

    .input{
        font-size: 15px;
    }

    .input::placeholder{
        font-size: 15px;
    }
}    