.card{
    width: 100%;
    padding: 1rem;
    border-radius: 1rem;
    background: #1F1F1F;
}

.title{
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.horizonatalBar{
    width: 100%;
    height: 1px;
    background: #383838;
    border-radius: 50%;
    margin: 0.5rem 0;
}

.orderDetail{
    display: flex;
    align-items: flex-start;
    gap: 1rem;
}

.icon{
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 0.5rem;
}

.orderName{
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.name{
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.planType{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #E2E2E2;
}

.headerDetail{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chevronRight{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
}
@media (min-width: 1280px){
    .title{
        font-size: 15px;
    }
    .name{
        font-size: 15px;
    }
    .planType{
        font-size: 17px;
    }
}