
.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2; 
    background: #0F0F0F;
}

.popUp{
    width: 100%;
    height: 100%;
    background: inherit;
}

.content{
    width: 91.1%;
    margin: 1rem auto 0 auto;
}

.couponHeader{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.couponText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronLeft{
    width: 1.75rem;
    height: 1.75rem;
    position: absolute;
    top: 0;
    left: -5px;
    transform: translate(0%,-10%);
}

.couponContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.typeYourCoupon{
    margin: 1rem 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 44px;
}

.inputWrapper{
    width: 73.7%;
    flex-grow: 0;
    flex-shrink: 0;
    height: 100%;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4px;
}

.crossSign{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: none;
}

.crossSign.display{
    display: block;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
   
}

.input::placeholder{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.applyButton{
    width: 22.5%;
    height: 100%;
    background: #00998C;
    border-radius: 16px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
    cursor: pointer;
}