
.card{
    width: 9.75rem;
    padding: 0.5rem;
    background: #1F1F1F;
    border-radius: 1rem;
    position: relative;
}

.card>img{
    width: 100%;
    height: 9rem;
    border-radius: 1rem;
    object-fit: cover;
}

.infoContainer{
    margin-top: 0.5rem;
}

.productName{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.priceContainer{
    margin-top: 0.3rem;
    display: flex;
    align-items: center;
    gap: 0.8rem;
    justify-content: flex-start;
}

.discountPrice{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #00C2B1;
}

.mainPrice{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.02em;
    text-decoration-line: line-through;
    color: #A8A8A8;
}

.discountContainer{
    height: 1.25rem;
    width: 3.375rem;
    padding: 4px 5.3px;
    border-radius: 6px;
    background: linear-gradient(180deg, #3881F8 0%, #1C457E 100%);
    font-family: 'Gilroy-Bold';
    font-size: 10px;
    line-height: 120%;
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #FFFFFF;
    position: absolute;
    right: 1rem;
    top: 0.8rem;
}

.brandLogo{
    display: none;
}

.brandName{
    display: none;
}

@media (min-width: 1280px){
    .card{
        width: 12.625rem;
        background: inherit;
        padding: 0;
    }

    .card>img{
        width: 100%;
        height: 12.625rem;
    }

    .infoContainer{
        margin-top: 0.2rem;
    }

    .brandInfoParent{
        margin-top: 0.5rem;
    }

    .priceContainer{
        margin: 0;
    }

    .brandLogo{
        display: block;
    }

    .brandName{
        display: block;
        font-family: 'Gilroy-Medium';
        font-size: 15px;
        line-height: 120%;
        display: flex;
        align-items: center;
        letter-spacing: 0.025em;
        color: #A8A8A8;

    }

    .allDetail{
        display: flex;
        gap: 0.5rem;
        align-items: center;
        justify-content: center;
    }
    .brandLogo{
        width: 3rem;
        height: 3rem;
        border-radius: 50%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .productName{
        font-family: 'Gilroy-Medium';
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #FAFAFA;
        width: 100%;
    }

    .discountPrice{
        font-family: 'Gilroy-Bold';
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #00C2B1;
    }

    .mainPrice{
        font-size: 15px;
    }
}