.card{
    width: 100%;
    background: #1F1F1F;
    border-radius: 1rem;
    padding: 1.625rem 1rem 1rem 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
}

.productDetail{
    display: flex;
    width: 100%;
    height: fit-content;
    align-items: center;
}

.logo{
    width: 42px;
    height: 42px;
    border-radius: 8px;
    flex-grow: 0;
    margin-right: 1rem;
}

.productDesc{
    width: 48%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 0;
}

.name{
    width: 100%;
    font-family: 'Gilroy-Regular';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.planName{
    width: 100%;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #E2E2E2;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.activateButton{
    display: flex;
    width: fit-content;
    height: fit-content;
    margin-left: auto;
    align-items: center;
}

.activateText{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #53A5F4;
    cursor: pointer;
}

.chevronRight{
    width: 1.5rem;
    height: 1.5rem;
}
@media (min-width: 1280px){
    .title{
        font-size: 17px;
    }

    .logo{
        width: 54px;
        height: 54px;
    }

    .name{
        font-size: 15px;
    }
    .planName{
        font-size: 17px;
    }

    .activateText{
        font-size: 15px;
    }
}