.card{
    width: 100%;
    padding: 1rem 1rem 1.5rem 1rem;
    background: #1F1F1F;
    border-radius: 16px;
}

.couponContainer{
    width: 100%;
    min-height: 8.625rem;
    position: relative;
    margin-bottom: 1rem;
}

.backGroundImage{
    width: 100%;
    height: 100%;
}

.upperText{
    width: 87%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 13%;
    border-left: 1px dashed #3B2F13;;
    padding: 1.625rem 1rem 1rem 1rem;
    letter-spacing: 0.025em;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.text1{
    font-family: 'Gilroy-Medium';
    font-size: 11px;
    line-height: 120%;
    color: #0F0F0F;
}

.text2{
    font-family: 'Gilroy-Bold';
    font-size: 21px;
    line-height: 140%;
    color: #0F0F0F;
}

.text2.isActivated{
    color: #616161;
}

.text3{
    font-family: 'Gilroy-Bold';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #0F0F0F;
    margin-top: auto;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    margin-bottom: 1rem;
}

.detail{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.singleDetail{
    margin-left: 1rem;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.singleDetail.isActivated{
    color: #616161;
}

.linkContainer{
    width: 100%;
    height: 1.5rem;
    background-color: #FAFAFA;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 4px;
}

.linkImg{
    width: 1rem;
    height: 1rem;
    flex-grow: 0;
}

.activeButton{
    width: 4.5rem;
    height: 100%;
    border: none;
    background: #0F0F0F;
    border-radius: 8px;
    font-family: 'Gilroy-Bold';
    font-size: 12px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.activeButton.isActivated{
    background: #616161;
}

.link{
    width: 55%;
    flex-grow: 0;
    font-family: 'Gilroy-Medium';
    font-size: 12px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #0F0F0F;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    /* margin: 0 4px; */
}

@media (min-width: 1280px){
    .title{
        font-size: 17px;
    }

    .singleDetail{
        font-size: 15px;
    }
    .couponContainer{
        height: 8.625rem;
    }
    .upperText{
        padding: 1rem;
        width: 91%;
        left: 9%;
    }
    .text1{
        font-size: 15px;
    }

    .text2{
        font-size: 21px;
    }
    
    .text3{
        font-size: 15px;
    }
    .linkContainer{
        height: 1.9rem;
        width: 18.1rem;
    }
    .link{
        font-size: 15px;
    }

    .activeButton{
        width: 5.75rem;
        flex-grow: 0;
        font-size: 15px;
    }
}