.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    z-index: 2; 
}

.popUp{
    max-width: 25.625rem;
    width: 90%;
    height: fit-content;
    background: #191919;
    border-radius: 1.5rem 1.5rem 0px 0px;
    padding: 0.6rem 1rem 0rem 1rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
}

.detailParent{
    position: relative;
    width: 100%;
}

.detail{
    width: 88%;
    text-align: center;
    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 14px;
    line-height: 23px;

    text-align: center;
    letter-spacing: 0.005em;

    color: #FAFAFA;

    margin: 0 auto;

}

.text1{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    margin-top: 0.2rem;
}

.cross{
    width: 1rem;
    height: 1rem;
    position: absolute;
    top: 0.3rem;
    right: 0.3rem;
    cursor: pointer;
}

.avatarContainer{
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    justify-content: space-between;
    row-gap: 1rem;
    column-gap: 2.5rem;
}

.userDetail{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    align-items: center;
    justify-content: space-between;
}

.avatarImg{
    width: 2.625rem;
    height: 2.625rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.userName{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    max-width: 65px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}