.card{
    width: 100%;
    padding: 0.3rem 0rem 0rem 1.5rem;
    border: 1px solid #493939;
    border-radius: 1rem;
    background: linear-gradient(232.73deg, rgba(40, 37, 37, 0.29) 30.16%, #282525 54.33%, rgba(40, 37, 37, 0) 77.67%);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.detail{
    width: 55%;
    flex-grow: 0;
}

.title1{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FFFFFF;
}

.title2{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    margin-top: 3px;
}

.bold{
    font-family: 'Gilroy-Bold';
}

.coinBundle{
    height: 66px;
    width: 39%;
}

@media (min-width: 1280px){
    .coinBundle{
        width: 8.18rem;
        height: 5rem;
    }
}