.BrandCard{
    width: 100%;
    margin: 0 auto;
    background-color: #1F1F1F;
    border-radius: 1rem;
    padding: 1rem;
    color: #FAFAFA;
    border: 1px solid #1f1f1f;
}

.BrandCard.disable{
    color: #616161;
}

.BrandCard.check{
    border: 1px solid #00C2B1;
}

.PlanMainInfo{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.radioBtn{
    display: block;
}

.Plan{
    margin-left: 0.75rem;
}

.Name{
    font-size: 1.063rem;
    font-weight: 500;
    line-height: 1.5rem;
    font-family: 'Gilroy-Medium';
} 

.Validity{
    font-size: 0.813rem;
    font-weight: 500;
    line-height: 1.125rem;
    color: #A8A8A8;
    letter-spacing: 0.025em;
    font-family: 'Gilroy-Medium';
}

.Validity.disable{
    color: #616161;
}

.HeadPrice{
    font-size: 1.188rem;
    line-height: 1.688rem;
    margin-left: auto;
    font-family: 'Gilroy-Medium';
}

.HeadPrice.disable{
    color: #616161;
}

.PriceDetailMob{
    margin-top: 1rem;
    background: #191917;
    border: 1px solid #228D88;
    border-radius: 0.5rem;
    padding: 0.8rem 0.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.PriceText{
    letter-spacing: 0.02em;
    font-size: 1rem;
    line-height: 1.25rem;
    font-weight: 500;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
}

.PayPrice{
    letter-spacing: 0.05em;
    font-size: 0.875rem;
    line-height: 1.125rem;
    color: #FAFAFA;
    font-weight: 700;
    margin-top: 2px;
    font-family: 'Gilroy-Bold';
}

.FleekPrice{
    color: #FFC747;
    letter-spacing: 0.05em;
    font-size: 0.875rem;
    line-height: 1.125rem;
    font-weight: 700;
    margin-top: 2px;
    font-family: 'Gilroy-Bold';
}

.Seperator{
    height: 1.125rem;
    margin: 0 0.3rem;
    width: 2px;
    background-color: #FAFAFA;
}

.Discount{
    letter-spacing: 0.05em;
    font-size: 0.875rem;
    line-height: 1.125rem;
    color: #FAFAFA;
    font-weight: 700;
    font-family: 'Gilroy-Bold';
}

.VerticleSpacer{
    width: 100%;
    border: 1px solid #383838;
    margin: 1rem auto;
    border-radius: 1px;
}

.Benefits{
    display: flex;
    flex-direction: column;
    gap: 0.625rem;
    font-family: 'Gilroy-Medium';
}

.BenefitContain{
    display: flex;
    align-items: center;
}

.Benefit{
    font-size: 0.813rem;
    font-weight: 500;
    line-height: 1.125rem;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.Benefit.disable{
    color: #616161;
}

.CheckLogo{
    width: 10px;
    height: 10px;
    margin-right: 1rem;
}

.message{
    display: flex;
    align-items: center;
}

.ExclamationIcon{
    width: 24px;
    height: 24px;
    margin-right: 8px;
    margin-bottom: 4px;
    object-fit: cover;
}

.MessageText{
    font-size: 0.813rem;
    font-weight: 500;
    line-height: 1.125rem;
    letter-spacing: 0.015em;
    color: #9FCDF9;
    font-family: 'Gilroy-Medium';
    margin-top: -5px;
}

.MessageText.disable{
    color: #616161;
}

.FleekCoin{
    width: 16px;
    height: 16px;
    margin: 0 4px;
}

.FleekPriceSec{
    display: flex;
    align-items: center;
}

.mobBenefit{
    display: block;
}

.deskBenefit{
    display: none;
}

.BenefitsContainer{
    transition: opacity 1s ease-out;
    opacity: 0;
    height: 0;
    overflow: hidden;
}

.BenefitsContainer.show{
    opacity: 1;
    height: auto;
}

.DeskActions{
    display: none;
}

.BuyNowButton{
    width: 9.375rem;
    border-radius: 1rem;
    height: 3rem;
    background: #00998C;
    color: #FAFAFA;
    font-size: 1rem;
    line-height: 1.25rem;
    font-family: 'Gilroy-Bold';
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-top: 0.8rem;
    cursor: pointer;
}

.mockCircle{
    width: 1.25rem;
    height: 1.25rem;
    border: 2px solid #616161;;
    border-radius: 50%;
    padding: 2px;
}

.disableText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #D1D1D1;
}

@media (min-width: 1280px) {
    .radioBtn{
        display: none ;
    }
    .Plan{
        margin: 0;
    }

    .BrandCard{
        width: 100%;
        padding: 1.5rem;
        position: relative;
        border: none !important;
    }
    .PlanMainInfo{
        align-items: flex-start;
    }
    .Name{
        font-size: 1.313rem;
    } 
    .Validity{
        font-size: 0.938rem;
        margin-top: 4px;
    }
    .HeadPrice{
        font-size: 1.313rem;
        color: #A8A8A8;
    }
    .PriceDetailMob{
        display: none;
    }

    .mobBenefit{
        display: none;
    }

    .deskBenefit{
        display: block;
    }

    .VerticleSpacer{
        display: none;
    }

    .Benefits{
        margin: 1rem 0;
    }

    .message{
        margin-left: -3px;
        align-items: center;
    }

    .Benefit{
        font-size: 1rem;
        max-width: 21rem;
    }

    .MessageText{
        font-size: 1rem;
        margin-top: -2px !important;
    }

    .DeskActions{
        display: block;
        position: absolute;
        bottom: 1.5rem;
        right: 1.4rem;
        margin-top: 1rem;
    }
    .PriceDetailDesk{
        margin-top: 1rem;
        background: #191917;
        border: 1px solid #228D88;
        border-radius: 0.5rem;
        padding:  0.6rem 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .disableText{
        font-size: 17px;
    }
}