.productPage{
    background: #0F0F0F;
}

.content{
    width: 91.2%;
    margin: 0rem auto;
    height: fit-content;
    background: #0F0F0F;
}

.productBoxContainer{
    width: 100%;
    margin-top: -1.2rem;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
    margin-bottom: 1.5rem;
}

.boxesAndPrice{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    width: 100%;
}

.boxes{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.priceInfo{
    width: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.markedPriceContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 0.5rem;
}

.markedPrice{
    text-decoration-line: line-through;
}

.actualPriceContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.actualPrice{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #00C2B1;
}

.buyButton{
    width: 100%;
    background: #00998C;
    border-radius: 16px;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    cursor: pointer;
}

@media (min-width: 1280px) {
    .productPage{
        width: 68.75%;
        max-width: 62rem;
        margin: 4rem auto;
    }

    .content{
        width: 100%;
    }

    .productBoxContainer{
        margin-top: 1.5rem;
    }

    .title{
        color: #FAFAFA;
        font-family: 'Gilroy-Bold';
        font-size: 19px;
    }
    .boxesAndPrice{
        flex-direction: row;
        justify-content: space-between;
        gap: 0;
        align-items: flex-start;
    }

    .boxes{
        width: 57%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .priceInfo{
        width: 41%;
        background: #1F1F1F;
        backdrop-filter: blur(10px);
        border-radius: 16px;
        padding: 24px 16px;
        font-size: 17px;
        position: sticky;
        top: 4rem;
    }
}    