.content{
    width: 91.1%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.userOption{
    width: 100%;
    padding: 1rem;
    background: #1F1F1F;
    border-radius: 1rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}

.userImgContainer{
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 50%;
    background: #404040;
    flex-grow: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.loginSingupButton{
    background: #00998C;
    border-radius: 16px;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    flex-shrink: 0;
    border: none;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
}

.userImg{
    width: 1.75rem;
    height: 1.75rem;
}

.otherLinkContainer{
    width: 100%;
    text-decoration: none;
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
}

.seperator1{
    width: 100%;
    height: 1px;
    background: #383838;;
    margin: 0.75rem 0 1.5rem 0;
}

.userImgLetter{
    font-family: 'Gilroy-Medium';
    font-size: 23px;
    line-height: 140%;
    color: #FAFAFA;
}

.userMainImg{
    width: 100%;
    height: 100%;
}

.userName{
    font-family: 'Gilroy-Medium';
    font-size: 19px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.userPhone{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
}

.Pencil{
    width: 1.5rem;
    height: 1.5rem;
    bottom: 0.2rem;
    right: 0;
    position: absolute;
}

.loginUserOptions{
    width: 100%;
    padding: 1.75rem 0 1.75rem 1.5rem;
    background: #1F1F1F;
    border-radius: 16px;
}

.loginUserOption{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    width: 100%;
}

.icon{
    width: 28px;
    height: 28px;
    flex-grow: 0;
}

.optionText{
    width: 88%;
    flex-grow: 0;
}

.optionText1{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.optionText2{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.seperator{
    width: 88%;
    height: 1px;
    background: #383838;
    position: relative;
    margin: 0.75rem 0 1.5rem auto;
}

.sideContextButton{
    width: 100%;
    background: #1F1F1F;
    border-radius: 16px;
    padding: 1rem;
}

.useFullLinkText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin: 1.5rem 0;
}


@media (min-width: 1280px){
    .desktopContainer{
        width: 68.75%;
        max-width: 61.875rem;
        margin: 0 auto;
    }

    .headText{
        font-family: 'Gilroy-Bold';
        font-size: 23px;
        line-height: 140%;
        color: #FAFAFA;
        margin-bottom: 2rem;
    }

    .parentContainer{
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    .container1{
        width: 31.5%;
        flex-grow: 0;
    }

    .container2{
        width: 65.7%;
        flex-grow: 0;
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
    }

    .otherLinkContainer{
        padding: 12px 16px;
        background: #1F1F1F;
        border-radius: 16px;
        font-size: 17px;
        color: #FAFAFA;
    }

    .otherLinkContainer a{
        cursor: pointer;
        display: inline-block;
        width: 100%;
    }

}