.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    z-index: 2; 
}

.popUp{
    width: 100%;
    background: #191919;
    border-radius: 1.5rem 1.5rem 0px 0px;
    padding: 1.625rem 1rem;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.detailBanner{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-bottom: 3rem;
}

.subtitleCont{
    display: flex;
    flex-direction: column;
}

.subhead1{
    line-height: 1.313rem;
    font-family: 'Gilroy-Bold';
    font-size: 1rem;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.subhead2{
    font-family: 'Gilroy-Medium';
    font-size: 0.75rem;
    line-height: 1rem;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.cross{
    width: 1.75rem;
    height: 1.75rem;
    display: inline-block;
    margin-left: auto;
    cursor: pointer;
}

.inputContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin-bottom: 1rem;

}

.inputHeading{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.inputBox{
    padding: 10px 12px;
    border: 0.5px solid #C7C7C7;
    border-radius: 12px;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    background: inherit;
    height: 44px;
    outline: none;
}

.popUpCta{
    padding: 12px 16px;
    height: 48px;
    background: #2E2E2E;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    cursor: pointer;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #616161;
}

@media (min-width: 1280px) {
    .popUp{
        width: 27.75rem;
        top: 50%;
        left: 50%;
        height: fit-content;
        transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
        padding-top: 3rem;
    }
} 