.card{
    padding: 1.875rem 1rem 2.25rem 1rem;
    background: #1F1F1F;
    border-radius: 1rem;
    width: 100%;
    margin: 0 auto;
}

.couponContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.coupon{
    font-family: 'Gilroy-Bold';
    font-size: 19px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.coupon.inactive{
    color: #616161;
}

.applyText{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #00998C;
    cursor: pointer;
}

.applyText.inactive{
    color: #616161;
}

.subText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin-top: 6px;
}

.subText.inactive{
    color: #ED3E35;
}

@media (min-width: 1280px){
    .applyText{
        font-size: 15px;
    }
}