.container{
    height: 2rem;
    padding: 0 0.75rem;
    background: #191919;
    border-radius: 1rem;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.FleekCoin{
    width: 0.813rem;
    height: 0.813rem;
    margin-bottom: 2px;
}

.priceText{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 12px;
    text-align: right;
    letter-spacing: 0.025em;
    color: #ECEDF0;
}

.verticalBar{
    height: 1rem;
    width: 1px;
    background-color: #383838;
}

.FleekWallet{
    width: 1.25rem;
    height: 1.25rem;
}

@media (min-width: 1280px){
    .container{
        height: 3rem;
        border-radius: 1.5rem;
        cursor: pointer;
    }

    .priceText{
        font-size: 1rem;
    }
}