.container{
    display: none;
}

.fleekicon{
    width: 5rem;
    height: 5rem;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 23px;
    line-height: 140%;
    color: #FAFAFA;
    margin-top: 1.5rem;
}

.subtitle{
    margin-top: 0.5rem;
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

@media (min-width: 1280px){
    .container{
        display: flex;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: fit-content;
        margin-bottom: 3rem;
    }


}