.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2; 
    background: #0F0F0F;
}

.popUp{
    width: 60.1%;
    height: fit-content;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 2rem 1rem 2rem 1rem;
    background: #1F1F1F;
    border-radius: 16px;
}

.CrossSign{
    display: none;
}

.paymentFail{
    width: 7.813rem;
    height: 7.813rem;
}

.title1{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #ED3E35;
}

.title2{
    font-family: 'Gilroy-Medium';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    margin-top: 1.5rem;
}

.button{
    width: 18.5rem;
    height: 3rem;
    background: #00998C;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
    margin-top: 1.5rem;
    cursor: pointer;
}

@media (min-width: 1280px){
    .popUp{
        position: relative;
        width: 36.5rem;
        padding: 5.6rem 1.625rem 5.6rem 1.625rem;
    }

    .CrossSign{
        display: block;
        position: absolute;
        top: 1rem;
        right: 1rem;
        width: 1.75rem;
        height: 1.75rem;
        cursor: pointer;
    }

    .title1{
        font-size: 17px;
    }

    .title2{
        font-size: 21px;
    }
}