.content{
    width: 91.1%;
    margin: 0 auto 8rem auto;
    position: relative;
}

.headcontent{
    display: none;
}

.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 1rem auto;
}

.headText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronLeft{
    width: 1.75rem;
    height: 1.75rem;
    position: absolute;
    top: 0;
    left: -5px;
    transform: translate(0%,-10%);
}

.detailContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.otherDetail{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.mobileButtonContainer{
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 29px 16px 42px 16px;
    background: #191919;
    backdrop-filter: blur(10px);
    border-radius: 32px 32px 0px 0px;
    width: 100vw;
}

.payButton{
    width: 91.1%;
    margin: 0 auto;
    height: 3rem;
    background: #00998C;
    border-radius: 16px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
}


.payButton.inactive{
    cursor: not-allowed;
    background: #2E2E2E;
    color: #616161;
}

@media (min-width: 1280px){
    .headText{
        font-size: 23px;
    }
    .header{
        margin-bottom: 1.5rem;
    }
    .content{
        width: 68.75%;
    }

    .detailContainer{
        width: 100%;
        flex-direction: row;
        gap: 0;
        justify-content: space-between;
    }
    .cardContainer{
        width: 57.15%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .otherDetail{
        width: 40.11%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .chevronLeft{
        display: none;
    }

    .header{
        justify-content: flex-start;
    }
    .headcontent{
        display: block;
        width: 100%;
    }
    .Navbar{
        display: flex;
        gap: 4px;
        align-items: center;
    }
    .fleekicon{
        width: 3.5rem;
        height: 3.5rem;
    }
    .fleektitle{
        font-size: 21px;
        color: #FAFAFA;
    }
    .mobileButtonContainer{
        position: relative;
        width: 100%;
        padding: 0;
        background: transparent;
    }
    .payButton{
        width: 100%;
        cursor: pointer;
    }
}