.BenefitCard{
    width: 100%;
    margin: 0 auto;
    background-color: #191919;
    border-radius: 0.75rem;
    padding: 1rem 0.8rem;
}

.ActionArea{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.BenefitTitle{
    font-family: 'Gilroy-Bold';
    font-size: 1rem;
    line-height: 1.25rem;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronDownClass{
    width: 1.75rem;
    height: 1.75rem;
}

.checvronDiv{
    display: inline-block;
    transition: all 5s ease-in-out;
}


.benefitTextContainer{
    padding: 0rem 1rem;
    margin-top: 4px;
}

.singleBenefit{
    font-size: 0.813rem;
    line-height: 1.625rem;
    letter-spacing: 0.02em;
    color: #A8A8A8;
    font-family: 'Gilroy-Medium';
    margin-bottom: 0.5rem;
}

@media (min-width: 1280px) {
    .BenefitCard{
        width: 100%;
        padding: 1.5rem;
        position: relative;
    }

    .ActionArea{
        cursor: pointer;
    }

    .singleBenefit{
        font-size: 1rem;
        line-height: 2.125rem;
    }
}    