.custom-radio {
    cursor: pointer;
}
  
.radio__input {
    display: none;
}
  
.radio__radio {
    width: 1.25rem;
    height: 1.25rem;
    /* border: 2px solid #FAFAFA; */
    border-radius: 50%;
    padding: 2px;
}

.radio__radio.checked{
    border: 2px solid #00C2B1;
}

.radio__radio.notChecked{
    border: 2px solid #FAFAFA;
}

  
.radio__radio::after {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    background: #00C2B1;
    border-radius: 50%;
    transform: scale(0);
    transition: transform 0.15s;
}
  
.radio__input:checked + .radio__radio::after {
    transform: scale(1);
}