.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 1rem auto 2rem auto;
}

.headText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.chevronLeft{
    width: 1.75rem;
    height: 1.75rem;
    position: absolute;
    top: 0;
    left: -5px;
    transform: translate(0%,-10%);
}

.otherDetail{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.userImgContainer{
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 50%;
    background: #404040;
    flex-grow: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.userImgLetter{
    font-family: 'Gilroy-Medium';
    font-size: 23px;
    line-height: 140%;
    color: #FAFAFA;
}

.userMainImg{
    width: 100%;
    height: 100%;
}

.CameraIcon{
    width: 1.25rem;
    height: 1.25rem;
    bottom: 0.2rem;
    right: 0;
    position: absolute;
}

.inputBundle{
    width: 100%;
    margin-top: 1.5rem;
}

.inputBundle.firstBundle{
    margin-top: 1rem;
}

.inputWrapper{
    height: 3rem;
    border: 0.5px solid #FAFAFA;
    border-radius: 0.875rem;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.inputWrapper.disable{
    border: 0.5px solid #525252;
}


.countryCode{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #A8A8A8;
}

.input{
    flex-grow: 1;
    margin-left: 0.3rem;
    border: none;
    background-color: inherit;
    outline: none;
    color: #FAFAFA;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    caret-color: #00998C;
    height: 2rem;
}

.input.disable{
    height: fit-content;
    color: #A8A8A8;
}

.input::placeholder{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.input::-webkit-calendar-picker-indicator{ 
    filter: invert(100%);
}


.crossSign{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: none;
}

.crossSign.display{
    display: block;
}

.inputTitle{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    margin-bottom: 4px;
}

.chipsContainer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
}

.chip{
    border: 1px solid #A8A8A8;
    border-radius: 48px;
    background-color: inherit;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 16px;

}

.chip.active{
    font-family: 'Gilroy-Bold';
    background: #00998C;
    border: none;
    color: #FAFAFA;
}

.mobileButtonContainer{
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 29px 16px 42px 16px;
    background: #191919;
    backdrop-filter: blur(10px);
    border-radius: 32px 32px 0px 0px;
    width: 100vw;
}

.payButton{
    width: 91.1%;
    margin: 0 auto;
    height: 3rem;
    background: #00998C;
    border-radius: 16px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
}


.payButton.inactive{
    cursor: not-allowed;
    background: #2E2E2E;
    color: #616161;
}

.spacer{
    width: 100%;
    height: 130px;
}

.overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    z-index: 2; 
}

.popUp{
    width: 100%;
    background: #191919;
    border-radius: 1.5rem 1.5rem 0px 0px;
    padding: 1.5rem 0;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.overlaytext1{
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    text-align: center;
    width: 100%;
}

.overlayBar{
    height: 1px;
    width: 100%;
    background: #2E2E2E;
    margin: 1rem 0 1.5rem 0;
}

.overlaytext2{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    margin: 0 auto 1.5rem 0;
    text-align: center;
    width: 100%;
}

.overlaytext2.red{
    color: #ED3E35;
}