.headcontent{
    display: none;
}

.content{
    width: 91.1%;
    margin: 0rem auto 8rem auto;
    position: relative;
}
.opacity25 {
    opacity: 25%;
}
.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    margin: 1rem auto;
}

.crossSign{
    width: 1.75rem;
    height: 1.75rem;
}

.headText{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.helpText{
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #53A5F4;
}

.congratsContainer{
    margin-top: 2rem;
    margin-bottom: 2.8rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.CongratsImage{
    width: 7.875rem;
    height: 7.875rem;
}

.congratsHead{
    font-family: 'Gilroy-Bold';
    font-size: 19px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin-top: 1rem;
    margin-bottom: 4px;
}

.congratsSubhead{
    font-family: 'Gilroy-Medium';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.detailContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.otherDetail{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.mobileButtonContainer{
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 29px 16px 42px 16px;
    background: #191919;
    backdrop-filter: blur(10px);
    border-radius: 32px 32px 0px 0px;
    width: 100vw;
}

.nextButton{
    width: 91.1%;
    margin: 0 auto;
    height: 3rem;
    background: #00998C;
    border-radius: 16px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
    padding: 0 1rem;
}


@media (min-width: 1280px){
    .headcontent{
        display: block;
        width: 100%;
    }
    .header{
        margin-bottom: 1.5rem;
    }
    .crossSign{
        display: none;
    }
    .content{
        width: 68.75%;
    }
    .headText{
        font-size: 23px;
    }
    .helpText{
        font-size: 15px;
        cursor: pointer;
    }
    .congratsHead{
        font-size: 21px;
    }
    .congratsSubhead{
        font-size: 17px;
    }
    .Navbar{
        display: flex;
        gap: 4px;
        align-items: center;
    }
    .fleekicon{
        width: 3.5rem;
        height: 3.5rem;
    }
    .fleektitle{
        font-size: 21px;
        color: #FAFAFA;
    }
    .detailContainer{
        width: 100%;
        flex-direction: row;
        gap: 0;
        justify-content: space-between;
    }
    .cardContainer{
        width: 57.15%;
        flex-grow: 0;
        flex-shrink: 0;
    }

    .otherDetail{
        width: 40.11%;
        flex-grow: 0;
        flex-shrink: 0;
    }
    .nextButton{
        width: 100%;
        cursor: pointer;
        font-size: 15px;
    }
    .mobileButtonContainer{
        position: relative;
        width: 100%;
        padding: 0;
        background: transparent;
    }
    .payButton{
        width: 100%;
        cursor: pointer;
    }

}

.forward{
    width: 1.5rem;
    height: 1.5rem;
}