.termsSection{
    width: 100%;
}

.termContainer{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    gap: 0.8rem;
}

.termContainer img{
    width: 1.5rem;
    height: 1.5rem;
}

.termText{
    width: 100%;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.025em;
}

.termText ul{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.termText li{
    margin-left: 1rem;
}

.termText .text{
    color: #A8A8A8;}

.termText .read_or_hide{
    color: rgb(68, 68, 191);
    cursor: pointer;
}
.sectionTitle{
    font-family: 'Gilroy-Bold';
    line-height: 1.313rem;
    font-size: 1rem;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin: 1.2rem 0;
}

@media (min-width: 1280px){
    .termText{
        font-size: 1.063rem;
        line-height: 1.5rem;
        font-family: 'Gilroy-Medium';
    }
    .sectionTitle{
        font-family: 'Gilroy-Bold';
        line-height: 1.688rem;
        font-size: 1.188rem;
        letter-spacing: 0.02em;
        color: #FAFAFA;
    }
}
