.content{
    width: 91.1%;
    margin: 1.5rem auto;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 48px);
    
}

.elements{
    width: 100%;
}

.mobileContent{
    width: 100%;
}

.chevron{
    width: 1.75rem;
    height: 1.75rem;
    margin-bottom: 1rem;
    margin-left: -0.5rem;
}

.title{
    font-family: 'Gilroy-Bold';
    font-size: 19px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
}

.subTitle{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
    margin-bottom: 1.5rem;
}

.otpContainer{
    display: flex;
    gap: 0.5rem;
    align-items: center;
    justify-content: flex-start;
}

.otpBox{
    width: 3rem;
    height: 3rem;
    border: 1px solid #525252;
    border-radius: 12px;
    background-color: inherit;
    outline: none;
    font-family: 'Gilroy-Bold';
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.otpBox.filled{
    border: 1px solid #C7C7C7;
}

.otpBox.error{
    border: 1px solid #ED3E35;
}

.otpBox:focus{
    border: 1px solid #C7C7C7;
}

.otpBox.error{
    border: 1px solid #ED3E35;
}

.incorrectText{
    font-family: 'Gilroy-Regular';
    font-size: 13px;
    line-height: 160%;
    letter-spacing: 0.025em;
    color: #ED3E35;
}

.subText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #808080;
    margin-top: 1.5rem;
}

.resendText{
    cursor: pointer;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #53A5F4;
}

.otpButton{
    height: 3rem;
    width: 100%;
    background: #2E2E2E;
    border-radius: 1rem;
    text-align: center;
    margin-top: auto;
    border: none;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.05em;
    font-family: 'Gilroy-Bold';
    color: #616161;
    pointer-events: none;
    cursor: not-allowed;
}

.otpButton.active{
    background: #00998C;
    color: #FAFAFA;
    pointer-events: all;
    cursor: pointer;
}

@media (min-width: 1280px){
    .mobileContent{
        display: none;
    }
    .content{
        width: 100%;
        height: 100vh;
        margin: 0;
        background: #191919;
        
    }

    .elements{
        width: 20.5rem;
        height: fit-content;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-55%);
        -ms-transform: translate(-50%,-55%);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .otpContainer{
        justify-content: space-between;
        width: 100%;
    }

    .subText{
        margin-bottom: 1.5rem;
    }
}