.brandPage{
    background: #0F0F0F;
    width: 100%;
}
.content{
    width: 91.2%;
    margin: 0rem auto;
    height: fit-content;
    background: #0F0F0F;
}

.displayDesktop{
    display: none;
}

.planList{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.planSection{
    width: 100%;
}

.planSection>p{
    margin: -1.2rem 0 1rem 0 !important;
}

.sectionTitle{
    font-family: 'Gilroy-Bold';
    line-height: 1.313rem;
    font-size: 1rem;
    letter-spacing: 0.02em;
    color: #FAFAFA;
    margin: 1.2rem 0;
}

.termsSection{
    width: 100%;
}

.termContainer{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    gap: 0.8rem;
}

.termContainer img{
    width: 1.5rem;
    height: 1.5rem;
}

.termText{
    width: 100%;
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.025em;

}

.termText>div{
    color: #A8A8A8;}

.termText>div>span{
    color: rgb(68, 68, 191);
    cursor: pointer;
}

.overViewSection{
    width: 100%;
}

.overViewText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.025em;
}

.overViewText>div{
    color: #808080;
}

.overViewText>div>span{
    color: rgb(68, 68, 191);
    cursor: pointer;
}

.benefitSection{
    width: 100%;
    margin-top: 1rem;
}

.faqsSection{
    width: 100%;
    margin-top: 1rem;
}

.boxContainer{
    width: 100%;
    margin-top: -1.2rem;
}

.d2cTitle{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
    margin-bottom: 1.5rem;
}

.boxes{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(9.75rem, 1fr));   
    row-gap: 1.2rem;
    column-gap: 0.8rem;
    justify-items: center;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.mobileButtonContainer{
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 29px 16px 42px 16px;
    background: #191919;
    backdrop-filter: blur(10px);
    border-radius: 32px 32px 0px 0px;
    width: 100vw;
    margin-top: 4rem;
}

.payButton{
    width: 91.1%;
    margin: 0 auto;
    height: 3rem;
    background: #00998C;
    border-radius: 16px;
    font-family: 'Gilroy-Bold';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    letter-spacing: 0.05em;
    color: #FAFAFA;
    border: none;
}

.mobSpacer{
    height: 125px;
}

@media (min-width: 1280px) {
    .brandPage{
        width: 68.75%;
        max-width: 62rem;
        margin: 4rem auto;

    }

    .mobileButtonContainer{
        display: none;
    }
    .content{
        width: 100%;
    }
    .planSection>p{
        margin: 1rem 0 1rem 0 !important;
    }

    .sectionTitle{
        font-family: 'Gilroy-Bold';
        line-height: 1.688rem;
        font-size: 1.188rem;
        letter-spacing: 0.02em;
        color: #FAFAFA;
    }
    .overViewText{
        font-size: 1.063rem;
        line-height: 1.5rem;
        font-family: 'Gilroy-Medium';
    }

    .boxContainer{
        margin-top: 1.5rem;
    }

    .d2cTitle{
        color: #FAFAFA;
        font-family: 'Gilroy-Bold';
        font-size: 19px;
    }

    .boxes{
        grid-template-columns: repeat(auto-fill, minmax(12.625rem, 1fr));
        justify-items: center;
        justify-content: center;
    }

    .mobSpacer{
        display: none;
    }

    .displayDesktop{
        display: block;
        width: 100%;
    }
}    