.card{
    width: 100%;
    margin: 0 auto;
    padding: 1.5rem 1rem;
    border-radius: 1rem;
    background: #1F1F1F;
}

.flexRow{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.itemContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1.25rem;
    margin-bottom: 0.875rem;
}

.paymentTitle{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #A8A8A8;
}

.noOfItemTitle{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #A8A8A8;
}

.itemName{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    display: flex;
    align-items: center;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.price{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
}

.fleekCoinText{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FAFAFA;
    display: flex;
    align-items: center;
}

.fleekCoin{
    width: 0.75rem;
    height: 0.75rem;
    margin-top: 3px;
}

.fleekPrice{
    font-family: 'Gilroy-Medium';
    font-size: 13px;
    line-height: 140%;
    letter-spacing: 0.025em;
    color: #FFE099
}

.horizontalBar{
    width: 100%;
    height: 1px;
    background: #383838;
    margin: 1rem 0;
    border-radius: 50%;
}

.totalText{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #00C2B1;
}

.totalPrice{
    font-family: 'Gilroy-Bold';
    font-size: 15px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #00C2B1;
}

@media (min-width: 1280px){
    .paymentTitle{
        font-size: 17px;
    }

    .noOfItemTitle{
        font-size: 15px;
    }

    .itemName{
        font-size: 17px;
    }

    .price{
        font-size: 17px;
    }

    .fleekCoinText{
        font-size: 17px;
    }
    .totalText{
        font-size: 19px;
    }
    .totalPrice{
        font-size: 17px;
    }
}